import { Injectable } from '@angular/core';

import { HttpClient, HttpEvent, HttpRequest } from '@angular/common/http';
import { BehaviorSubject, Observable } from 'rxjs';
import * as FileSaver from 'file-saver';

import * as XLSX from 'xlsx';
import { DatePipe } from '@angular/common';

import { AppConstants } from 'src/app/shared/config/app.constant';
import { FetchUserForSingleAccClosureReqDTO } from '../models/FetchUserForSingleAccClosureReqDTO';

export const API_URL = AppConstants.acctCloserUrl;
const EXCEL_TYPE = 'application/vnd.openxmlformats-officedocument.spreadsheetml.sheet;charset=UTF-8';
const EXCEL_EXTENSION = '.xlsx';
@Injectable({
  providedIn: 'root'
})
export class AccountClosureServiceService {

  exceldatafinal: any;
  datafinal = [];
  constructor(private http: HttpClient) {


  }
  private paramSource = new BehaviorSubject({});
  getNavParam = this.paramSource.asObservable();
  private Index = new BehaviorSubject<any>({
    index: '',
  })
  setIndexValue(index: any) {
    this.Index.next(index);
  }

  getIndexValue() {
    return this.Index.asObservable();
  }
   sendNavParam(params: object) {
    this.paramSource.next(params)
  }
  excel = [];
  onSingleClosure(data, inputby: string) {
    console.log(data);
    return this.http.post<FetchUserForSingleAccClosureReqDTO>(`${API_URL}/api2/createAccountClosure/${inputby}`, data);
  }
  onViewOfSingleClsureType(accType: string, closureType: string, accountNumber: string) {
    console.log("this is in service of ");
    return this.http.get<FetchUserForSingleAccClosureReqDTO>(`${API_URL}/api2/getDetailsSingleReport/${accType}/${closureType}/${accountNumber}`)
  }
  gettingAccountClosureSummary(currentUser) {
    return this.http.get(`${API_URL}/api2/getSummary/${currentUser}`);
  }
  // -------------After Edit of Bulk Closure----------
  gettingEditData(inputBy: string) {
    return this.http.get(`${API_URL}/api2/modify/${inputBy}`)
  }
  submiitingSingleClosureRecordAfterEdit(data, inputBy: string, id: any) {
    console.log("single edit ", id);
    return this.http.post<FetchUserForSingleAccClosureReqDTO>(`${API_URL}/api2/modify/${inputBy}/${id}`, data)
  }
  //--------------file upload-------------

  onFileUpload(file: File, accountType: string, closure: string, inputBy: string) {
    console.log("this is in service", file);
    console.log("checing null or not", accountType)
    console.log(closure);
    const formData = new FormData();
    formData.append('file', file);

    return this.http.post<any>(`${API_URL}/file/saveFileDataToDB/${accountType}/${closure}/${inputBy}`, formData)
    
  }
  // ------------- Excel Download without header color------------
  public exportAsExcelFile(json: any[], excelFileName: string): void {
    console.log(excelFileName);
    console.log(json);
    const worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(json);

    const workbook: XLSX.WorkBook = { Sheets: { 'data': worksheet }, SheetNames: ['data'] };
    const excelBuffer: any = XLSX.write(workbook, { bookType: 'xlsx', type: 'array', });

    this.saveAsExcelFile(excelBuffer, excelFileName);
  }
  private saveAsExcelFile(buffer: any, fileName: string): void {
    const data: Blob = new Blob([buffer], { type: EXCEL_TYPE });

    FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
  }
  // --------------Excel Download for Single---------------
  public exportAsExcelForSingleAccount(json: any[], excelFileName: string): void {
    console.log(excelFileName);
    console.log(json);
    const worksheet: XLSX.WorkSheet = XLSX.utils.json_to_sheet(json);
    worksheet.blankRows = false;

    const workbook: XLSX.WorkBook = { Sheets: { 'data': worksheet }, SheetNames: ['data'] };
    const excelBuffer: any = XLSX.write(workbook, { bookType: 'xlsx', type: 'array' });

    this.saveAsExcelSingle(excelBuffer, excelFileName);
  }
  private saveAsExcelSingle(buffer: any, fileName: string): void {
    const data: Blob = new Blob([buffer], { type: EXCEL_TYPE });

    FileSaver.saveAs(data, fileName + '_export_' + new Date().getTime() + EXCEL_EXTENSION);
  }


  // -------------------Excel Download color---------------------------


  //getting excel for bulk
  getExcel(filename, accountType) {
    console.log("this is send fileName and getFile", accountType)
    console.log(filename)

    return this.http.get<any>(`${API_URL}/file/download/${filename}/${accountType}`);



  }

  onDeletingTheReocrd(fileName: string, accountType: string, inputBy: string) {
    console.log("file", fileName, "accType", accountType, "maker", inputBy)
    return this.http.get(`${API_URL}/file/cancelProcess/${accountType}/${fileName}/${inputBy}`)
  }
  onDeletingSingle(accountNumber: string, accntType: string, closure: string, maker: string) {
    console.log("in service", accountNumber, accntType, closure, maker)
    return this.http.get(`${API_URL}/api2/cancelProcess/${accountNumber}/${accntType}/${closure}/${maker}`)
  }
    // -----------Process the Bulk record---------
    processTheBulkRecord(fileName: string, accountType: string, inputBy: string) {
      console.log("file", fileName, "accType", accountType, "maker", inputBy)
      return this.http.get<FetchUserForSingleAccClosureReqDTO>(`${API_URL}/api2/process/${fileName}/${accountType}/${inputBy}`)
    }
    // -----------Process the Single record---------
    processTheSingleRecord(accountNumber: string, inputBy: string) {
      console.log(accountNumber, "maker", inputBy)
      return this.http.get<FetchUserForSingleAccClosureReqDTO>(`${API_URL}/api2/processStatus/${accountNumber}/${inputBy}`)
    }

     // ----------------Adit Log Method-----------------
  onAuthorizingTheRecord(accountNumber: string, authorizer: string) {
    console.log("this is authorization");
    return this.http.get<boolean>(`${API_URL}/api2/verify/${accountNumber}/${authorizer}`)
  }

  onClosingTheRecord(accountNumber: string, inputBy: string) {
    console.log("this is Closing Action");
    return this.http.get<FetchUserForSingleAccClosureReqDTO>(`${API_URL}/api2/close/${accountNumber}/${inputBy}`)
  }
  onReopningTheRecord(accountNumber: string, inputBy: string) {
    return this.http.get<FetchUserForSingleAccClosureReqDTO>(`${API_URL}/api2//${accountNumber}/${inputBy}`)
  }

}





